html {
  background: #f1f1f1;
}

body {
  padding: 2rem 0;
  font-family: Azeret Mono, monospace;
}

@keyframes tailSwish {
  0% {
    rotate: none;
  }

  25% {
    rotate: -2deg;
  }

  27% {
    rotate: -28deg;
  }

  30% {
    rotate: -25deg;
  }

  36% {
    rotate: -25deg;
  }

  50% {
    rotate: -5deg;
  }

  75% {
    rotate: 10deg;
  }

  78% {
    rotate: 5deg;
  }

  98% {
    rotate: 5deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes headLook {
  0% {
    rotate: none;
  }

  60% {
    rotate: none;
  }

  80% {
    rotate: -12deg;
  }

  100% {
    rotate: none;
  }
}

h3 {
  color: #e46153;
}

#whiskerStage {
  min-height: 670px;
  background: #dadada;
  border-radius: 1rem;
  margin: 3rem 0;
  padding: 1.5rem;
  position: relative;
  overflow: hidden;
}

#whiskerStage .button {
  z-index: 1100;
  position: relative;
}

#whiskerStage:after {
  opacity: .45;
  width: 100%;
  content: " ";
  z-index: 1;
  background-color: #f6c067;
  background-image: linear-gradient(45deg, #f1f1f1 20%, #0000 20% 80%, #f1f1f1 80% 100%), linear-gradient(135deg, #f1f1f1 20%, #0000 20% 80%, #f1f1f1 80% 100%);
  background-position: 0 0, 0 0;
  background-repeat: repeat, repeat;
  background-size: 3em 3em;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  display: block;
  position: absolute;
  inset: 0 20% 0 0;
}

#whiskerStage nav, #whiskerStage aside, #whiskerStage progress, #whiskerStage span.tag {
  z-index: 100;
  position: absolute;
}

#whiskerStage nav {
  position: absolute;
  bottom: 1.2vw;
  left: 0;
  right: 0;
}

#whiskerStage nav .button {
  width: 17%;
  min-height: 4rem;
  color: #333;
  background: #f6c067;
  border-radius: 1rem;
  margin: .5%;
  padding: 1.5rem 0;
  transition: all .3s ease-in-out;
  box-shadow: 0 .25rem .125rem #64646440;
}

#whiskerStage nav .button:hover {
  outline: 2px solid green;
}

#whiskerStage nav .button:first-of-type {
  margin-left: 2%;
}

#whiskerStage nav .button:last-of-type {
  margin-right: 2%;
}

#whiskerStage nav .button em {
  margin-top: .5vw;
  padding-right: .67rem;
  display: inline-block;
  position: relative;
}

#whiskerStage nav .button em img {
  max-width: 3vw;
  max-height: 3vw;
}

#whiskerStage progress {
  width: 63%;
  transition: all .3s;
  position: absolute;
  top: 2.333rem;
  left: 2%;
}

#whiskerStage .tag.game-time {
  border-radius: 2.75rem;
  font-size: 2rem;
  position: absolute;
  top: .8rem;
  right: 22.5%;
}

#whiskerStage .tag.game-time img {
  width: 3rem;
  height: auto;
  margin: 1rem .5rem 0 -.5rem;
}

#whiskerStage .modal {
  z-index: 800;
  justify-content: flex-start;
  position: absolute;
  top: 7.5rem;
  left: 2.5%;
  right: 2.5%;
}

#whiskerStage .modal .modal-content {
  height: 25rem;
  width: 100%;
  align-self: flex-start;
  margin: 0 auto;
  display: flex;
  max-height: auto !important;
}

#whiskerStage .modal .box {
  z-index: 810;
  width: 42.5%;
  min-height: 20rem;
  background-color: #fff;
  border-radius: 2rem;
  padding: 3rem;
  overflow: hidden;
}

#whiskerStage .modal .box h1 {
  color: #333;
  text-shadow: -.35vw .35vw #edc0a8;
  font-size: 3.5rem;
  font-weight: bold;
  line-height: 1;
}

#whiskerStage .modal .box .button {
  background: #e46153;
  margin-top: 1rem;
  padding: 1.5rem 3rem;
  font-family: Azeret Mono, monospace;
  font-size: 1.5rem;
  font-weight: bold;
}

#whiskerStage #gameFinishModal .modal-content {
  height: 27rem !important;
}

#whiskerStage #gameFinishModal .modal-content h1 {
  font-size: 3.5rem;
}

#whiskerStage #gameFinishModal .modal-content .button#gameStart {
  margin-top: .75rem;
  font-family: Azeret Mono, monospace;
}

#whiskerStage #gameFinishModal .modal-content .leaderboard {
  width: 42.5%;
  height: 9rem;
  background-color: #fcf6f2;
  padding: 2rem;
  position: absolute;
  top: 18rem;
  left: 0;
}

#whiskerStage #gameFinishModal .modal-content .button.is-info {
  color: #6b9cb3;
  background-color: #dadada;
  margin: 0;
  padding: 1.2rem;
  font-size: .825rem;
}

#whiskerStage #gameFinishModal .modal-content p small {
  font-size: .75rem;
}

#whiskerStage aside {
  width: 20%;
  background: #dadada;
  border-left: 1px dashed #fff;
  padding: 2rem;
  top: 0;
  bottom: 0;
  right: 0;
}

#whiskerStage aside strong {
  float: right;
  margin: 0 1.5rem;
}

#whiskerStage aside ul {
  border-top: 1px solid #f1f1f1;
  margin: 2rem .125rem 0;
  padding-top: 2rem;
}

#whiskerStage aside ul li {
  line-height: 2;
  list-style: none;
}

#whiskerStage .slats-the-cat {
  width: 20rem;
  height: 30rem;
  z-index: 5;
  background: url("slats-torso.64e56eb6.svg") 0 0 / contain no-repeat;
  position: absolute;
  bottom: -1.5rem;
  right: 25%;
}

#whiskerStage .slats-the-cat .slats-head {
  animation: headLook 12s linear infinite;
  position: absolute;
  top: -8rem;
  left: -2rem;
}

#whiskerStage .slats-the-cat .slats-tail {
  width: 25rem;
  min-height: 15rem;
  transform-origin: 100% 0;
  background: url("slats-tail.5082d04e.svg") 0 0 / contain no-repeat;
  animation: tailSwish 12s linear infinite;
  display: inline-block;
  position: relative;
  top: 15rem;
  right: 5rem;
}

.slats-head {
  width: 12rem;
  min-height: 25rem;
  background: url("slats-head.d381f18c.svg") 0 0 / contain no-repeat;
  display: inline-block;
  position: relative;
}

.slats-head img {
  width: 100%;
  display: none;
  position: absolute;
  top: 6.33rem;
  left: -1.25rem;
}

.slats-head.slats-resting img.is-resting, .slats-head.slats-blink img.is-blink, .slats-head.slats-eating2 img.is-eating2, .slats-head.slats-eating img.is-eating, .slats-head.slats-huh img.is-huh, .slats-head.slats-grumpy img.is-grumpy {
  display: inline-block;
}

#elements {
  position: relative;
}

#elements svg, #elements img {
  max-width: 20vw;
  height: auto;
}

#elements svg h3, #elements svg h4, #elements img h3, #elements img h4 {
  color: #e46153;
  margin: 1rem 0;
  font-weight: bold;
}

#elements ul {
  margin: 2rem 0;
}

#elements li {
  padding-top: 3rem;
}

#elements li .slats-head {
  padding-top: 16rem;
}

#elements li code {
  color: #333;
  background: #dadada;
}

